import React, { useContext, useState } from 'react';
import { getUrl } from '../services/api';
import { store } from '../services/store';

const BibleVerse = (props) => {
	const { verse } = props

	const [visible, setVisible] = useState(false);
	const [bibleText, setBibleText] = useState({
		verses: [],
		book: {
			title: '',
			translation: ''
		},
		query: {
			chapter: '',
			verse: ''
		}
	});

	const open = () => {
		fetchVerse()
		setVisible(true);
	}

	const { state } = useContext(store)

	const args = {
		controller: 'bible',
		method: 'search',
		query: verse,
		lang: state.lang
	}

	const url = getUrl('/', args)

	const fetchVerse = async () => {
		fetch(url).then((response) => 
			response.json()
		).then((data) => { 
			setBibleText(data)
		}).catch((error) => {
			console.log(error)
		})
	}

	const close = () => {
		setVisible(false);
	}
	return (
		<a href='#/' tabIndex={0} onFocus={open} onBlur={close}>{props.children}
			<div className={`popup ${visible ? 'popup--active' : ''}`}>
				<div className='popup__head'>
					<h5 className='popup__title'>{bibleText.book?.title} {bibleText.query?.chapter},{bibleText.query?.verse}</h5>
					<small>{bibleText.book?.translation}</small>
				</div>
				<div className='popup__body'>
					{ bibleText?.verses?.length == 0 && <span className="loading"></span> }
					{ bibleText?.verses?.length > 0 && Object.values(bibleText.verses).map(verse => {
						return <span><sup>{verse.verse}</sup>{verse.text}</span>
					}) }
				</div>
			</div>
		</a>
	)
}

export default BibleVerse