import React, { useContext, useEffect, useState } from 'react';
import { useParams } from "react-router-dom";
import Navigation from '../partials/Navigation';
import { getUrl } from '../services/api';
import { store } from '../services/store';

const Bible = () => {
	const params = useParams();

	const { book, chapter, verse } = params;

	const { state } = useContext(store)

	const [bible, setBible] = useState({
		verses: [],
		book: {}
	})

	console.log(state);
	
	let bookId = 10;
	if (state.site.bible !== undefined) {
		bookId = state?.site?.bible?.find(item => item.short_name === book);
		console.log(bookId)
	}
	
	useEffect(() => {
		const args = {
			controller: 'bible',
			lang: state.lang,
			book: bookId,
			chapter
		}
		const url = getUrl('/',args)
		
		fetch(url).then((response) => 
			response.json()
		).then((data) => { 
			setBible(data)
		})
		
	}, [book, chapter]);

	  

	return (
		<>
		<Navigation />
		<header className="header" style={{paddingTop: '20vh'}}>
			
			<div className="content">
				<div className="header__title">
					<h1 className="font-script">{bible?.book?.title}</h1>
					<h4 className="ml-4 xl:mr-0">{bible?.book?.chapter}</h4>
				</div>
			</div>
			
		</header>
		<div className='section bg-gray-300 py-12'>
			<div className='content'>
				CONTROLS
			</div>
		</div>
		<section>
			<div className='container'>
			<ul>
				{ bible?.verses?.map((verse) => {
					return <li><sup>{verse.verse}</sup>{verse.text}</li>
				}) }
			</ul>
			</div>
		</section>
		</>
	)
}

export default Bible