
/**
 * Formats two dates to a date range
 * @param {Date} start 
 * @param {Date} end 
 * @returns string formatted date
 */
function formatDateRange(start, end = false) {

	const locale = getBrowserLocale();
	
	start = new Date(start * 1000);
	end = end ? new Date(end * 1000) : start;

	const sameDay = start.getFullYear() === end.getFullYear() &&
		start.getMonth() === end.getMonth() &&
		start.getDate() === end.getDate();
	
	let dateFormat = {
		year: 'numeric',
		month: 'long',
		day: 'numeric',
		
	};

	if(sameDay) {
		dateFormat = {
			year: 'numeric',
			month: 'long',
			day: 'numeric',
			hour: 'numeric',
    		minute: 'numeric'
		};
	}

	const dateFormatObject  = new Intl.DateTimeFormat(locale, dateFormat);
	
	return dateFormatObject.formatRange(start, end);
	
	
}

/**
 * format date by goiven format object
 * @param {Date} date 
 * @param {object} format 
 * @returns string formated date
 */
function formatDate(date, format) {
	const locale = getBrowserLocale();
	const dateFormatObject  = new Intl.DateTimeFormat(locale, format);
	return dateFormatObject.format(date * 1000);
}


function formatTime(time) {
	const locale = getBrowserLocale();

	const timeFormat = {
		hour: 'numeric',
		minute: 'numeric'
	};

	const timeFormatObject  = new Intl.DateTimeFormat(locale, timeFormat);
	return timeFormatObject.format(time * 1000);
}

function diffDate(date, diff) {
	const then = new Date(date*1000)
	const now = new Date()
	var diff = (now - then);
	return Math.floor(diff / 1000 / 60 / 60 / 24 / 7 / 4)
}

function getBrowserLocale() {
	return navigator.languages && navigator.languages.length ? navigator.languages[0] : navigator.language;
}

export { formatDateRange, formatDate, formatTime, diffDate };
