import React, { useContext, useEffect, useState } from 'react';
import { getUrl } from '../services/api';
import { store } from '../services/store';
import Card from './Card';

const ArticleCards = (props) => {

	const { state } = useContext(store)
	const { query, value, columns = 3 } = props;

	const [ pages, setPages ] = useState([])
	const [ loading, setLoading ] = useState(true)

	useEffect(() => {

		const args = {
			controller: 'pages',
			query,
			value,
			lang: state.lang
		}
		fetch(getUrl('/', args)).then((response) => 
			response.json()
		).then((data) => { 
			setPages(data)
			setLoading(false)
		})
		
	  }, [value]);


	return (
		<>
		{ loading && <div className={`grid grid--columns-${columns} grid--gap-12`}>{[...Array(6)].map((page, index) => {
			return(<div key={index} className='card card--image-top card--white card--shadow card--loading'>
				<div className='card__image'>
					
				</div>
				<div className='card__content'>
					<div className='card__title'>
						
					</div>
					<div className='card__text'>
						
					</div>
				</div>
				
				</div>)
		}) }</div> }
		<div className={`grid grid--columns-1 md:grid--columns-2 xl:grid--columns-${columns} grid--gap-12 mt-12`}>{ pages?.map((page, index) => {
			return (
				<Card 
					key={index}
					image={page.image?.src}
					title={page.title}
					label={page.category}
					text={page.abstract}
					link={page.id}
				/>
			)
		}) }</div>
		</>
	)
}

export default ArticleCards