import parse from 'html-react-parser';
import React, { useContext, useEffect, useState } from 'react';

import { Link, useParams } from "react-router-dom";
import Navigation from '../partials/Navigation';
import { getLink, getUrl } from '../services/api';
import { store } from '../services/store';
import __ from '../services/translate';
const SearchPage = (props) => {
	
	const params = useParams();
	const queryParam = Object.values(params)[0]

	const { state } = useContext(store)
	const [ query, setQuery ] = useState(queryParam)
	const [ queryField, setQueryField ] = useState(queryParam)
	const [ results, setResults ] = useState({
		items: [],
		categories: []
	})

	const menu = state.site.menu

	const [ category, setCategory ] = useState('')

	useEffect(() => {
		const args = {
			controller: 'search',
			q: query,
			lang: state.lang
		}
		const url = getUrl('/',args)
		fetch(url).then((response) => 
			response.json()
		).then((data) => { 
			setResults(data)
		})
		
	}, [query]);

	const filteredResult = () => {
		if(category === '') return results.items;
		return results.items.filter(item => {
			return item.category == category
		})
	}

	const changeSearch = (event) => {
		setQueryField(event.target.value)
		console.log(queryField)
		if(event.key !== 'Enter') return;
		setQuery(event.target.value);
	}

	  console.log(results)
	
	return (
		<>
		<Navigation menu={menu}/>
		<header className="header" style={{paddingTop: '10vh'}}>
			
			
			
			<div className="content">
				<div className="header__title">
					<h1 className="font-script">{__("Search")}</h1>
					<h4 className="ml-4 xl:mr-0">{query}</h4>
				</div>
			</div>
			
		</header>
		<section className='bg-gray-200 py-12'>
			<div className='content'>
				<div className='input mb-12'><label>{__("Search")}</label><input value={queryField} type="text" onChange={(event) => setQueryField(event.target.value)} onKeyDown={(event) => changeSearch(event)}/></div>
				{__("Select a category")}
			<div className='pills '>
			<li onClick={() => {setCategory('')}} className={"pills__item " + (category === '' ? 'pills__item--primary' : '')}>All</li>
			{ results.categories.map((cat, index) => {
				return <li className={"pills__item " + (cat == category ? 'pills__item--primary' : '')} key={index} onClick={() => {setCategory(cat)}}>{cat}</li>
			}) }
			
			</div>
			</div>
		</section>
		<section className="bg-gray-100 py-12">
			<div className="content">
				<div className='list'>
				{ filteredResult().map((item, index) => {
					return <Link className='list__item' to={getLink(item.id)}>
						{ item.image && <img className='list__image list__image--edgy' src={getUrl('_media/' + item.image, {w: "200", lang: state.lang})}/> }
						{ !item.image && <div className='list__icon'>
							<i className='material-icons'>{item.icon}</i>	
						</div>}
						<div className='list__content'>
							<span className='list__title'>{item.title}</span>
							<span className='list__description'>{parse(item.abstract)}</span>
						</div>
					</Link>
				}) }
				</div>
			</div>
		</section>
		<section className='bg-gray-300 py-8'>
			
		</section>
		</>
	)
}

export default SearchPage