import { useContext, useEffect, useRef, useState } from 'react'
import { store } from './store'

function useFetch(url, args = {}, relative = false) {
  
  const {state} = useContext(store)
  // Used to prevent state update if the component is unmounted
  const cancelRequest = useRef(false)

  const [response, setResponse] = useState({
    status: undefined,
    data: undefined,
  })

  

  

  useEffect(() => {
    // Do nothing if the url is not given
    if (!url) return
	console.log(url)
    cancelRequest.current = false

    const fetchData = async () => {
      setResponse((response) => { return {...response, status: 'loading'}});

      try {
        const response = await fetch(url)
        if (!response.ok) {
          throw new Error(response.statusText)
        }

        const data = (await response.json())
        if (cancelRequest.current) return

        setResponse((response) => { return {...response, status: 'success', data: data}});
      } catch (error) {
        if (cancelRequest.current) return

        setResponse((response) => { return {...response, status: 'error', data: error}});
      }
    }

    void fetchData()

    // Use the cleanup function for avoiding a possibly...
    // ...state update after the component was unmounted
    return () => {
      cancelRequest.current = true
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [url])

  return response
}

export default useFetch