import { useContext, useEffect } from "react";
import {
	BrowserRouter, Route, Routes
} from "react-router-dom";

import './App.css';
import Article from "./pages/Article";
import Bible from './pages/Bible';
import SearchPage from "./pages/SearchPage";
import Tag from "./pages/Tag";
import Footer from "./partials/Footer";
import './scss/style.scss';
import { getUrl } from "./services/api";
import getTopLevelDomain from "./services/getTopLevelDomain";
import { store } from './services/store.js';



function App() {
	const globalState = useContext(store);
	const { dispatch, state } = globalState;

	const currentLanguage = state.lang;
	
	useEffect(() => {
		const args = {
			controller: 'site',
			lang: currentLanguage
		};
		const url = getUrl('/', args);
		fetch(url).then((response) => response.json()
		).then((data) => {
			dispatch({ type: 'SET_SITE', payload: data });
		});

	}, [currentLanguage]);

	return (
		<div className={'App ' + getTopLevelDomain()}>
			<BrowserRouter>
				<Routes>
					<Route path="bibel/:book/:chapter" element={<Bible />} />
					<Route path="search/:query" element={<SearchPage />} />
					<Route path="tag/:tag" element={<Tag />} />
					<Route path="*" element={<Article />} />
				</Routes>
			</BrowserRouter>
			<Footer />

		</div>
	);
};

export default App;
