import React, { useContext } from 'react';
import { Link } from 'react-router-dom';
import { getLink, getUrl } from '../services/api';
import { store } from '../services/store';

const Card = (props) => {
	const {
		image, title, subtitle, text, link, label, actions, badge
	} = props;

	const { state } = useContext(store)
	
	return (<Link className='card card--image-top card--white card--shadow card--hover card--primary' to={getLink(link)}>
		<div className='card__image'>
			{ badge && <b className='card__badge'>{badge}</b> }
			{ image && 
			<img src={getUrl(image, {w: 600, lang: state.lang})} /> }
			{ !image && <div className='card__image--replace'></div>}	
		</div>
		<div className='card__content'>
			<div className='card__label'>{label}</div>
			<div className='card__title'>
				{title}
			</div>
			<div className='card__subtitle'>
				{subtitle}
			</div>
			<div className='card__text'>
				{text}
			</div>
		</div>
	</Link>)
}

export default Card