import { useContext } from 'react';

import { store } from './store';
const SERVER_URL = process.env.REACT_APP_SERVER_URL;

function getUrl(path, params = {}, relative = false) {
	
	
	if(path[0] != '/') path = '/' + path;
	path = path.replaceAll(':', '/')
	if(Object.keys(params).length === 0) return relative ? path : SERVER_URL + path;

	let args = Object.entries(params).map(([key, value], index) => {
		return key + '=' + encodeURI(value)
	}).join('&')
	return SERVER_URL + path + '?' + args
}

function useUrl(path, params = {}, relative = false) {
	const {state} = useContext(store)
	
	if(path[0] != '/') path = '/' + path;
	path = path.replaceAll(':', '/')
	if(Object.keys(params).length === 0) return relative ? path : SERVER_URL + path;

	let args = Object.entries(params).map(([key, value], index) => {
		return key + '=' + encodeURI(value)
	}).join('&')
	return SERVER_URL + path + '?' + args
}

function getMedia(id, size) {
	return SERVER_URL + '/_media' + id + '?w=' + size;
}

function getLink(id) {
	let path = id.replaceAll(':', '/')
	if(path[0] != '/') path = '/' + path;
	return path;
}

export { useUrl, getUrl, getMedia, getLink };
