import React, { useContext, useEffect, useState } from 'react';
import { useParams } from "react-router-dom";
import Card from '../partials/Card';
import Navigation from '../partials/Navigation';
import { getUrl } from '../services/api';
import { diffDate } from '../services/formatDate';
import { store } from '../services/store';
import __ from '../services/translate';

const Tag = () => {
	
	const { state } = useContext(store)
	const params = useParams();
	let tagName = params.tag;
	
	
	const [ tag, setTag ] = useState({
		root: {
			title: "",
			subtitle: ""
		},
		articles: []
	})
	
	useEffect(() => {
		const args = {
			controller: 'pages',
			method: 'get_tag',
			tag: tagName,
			lang: state.lang
		}
		const url = getUrl('/',args)
		
		fetch(url).then((response) => 
			response.json()
		).then((data) => { 
			setTag(data)
		})
		
	  }, [tagName]);

	  
	
	return (
		<>
		<Navigation />
		<header className="header" style={{paddingTop: '30vh'}}>
			<div className="content">
				<div className="header__title">
					<h1 className="font-script">{tag.root?.title}</h1>
					<h4 className="ml-4 xl:mr-0">{tag.root?.subtitle}</h4>
				</div>
			</div>
		</header>
		<section className="bg-gray-100 py-12">
			<div className='content'>
				<div className='grid grid--columns-1 md:grid--columns-2 xl:grid--columns-3 grid--gap-12 mt-12'>
				{ tag.articles.map((article, index) => {
					return <Card 
					key={index}
						title={article.title}
						label={article.category}
						text={article.abstract}
						image={article.image?.src}
						link={article.id}
						badge={diffDate(article.date) < 3 ? __('New') : ''}
					/>
				}) }
				</div>
			</div>
		</section>
		
		</>
	)
}

export default Tag