import React, { useContext, useEffect, useRef, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { getUrl } from '../services/api';
import { store } from '../services/store';

const MenuSearch = (props) => {
	const {state} = useContext(store);
	const { setMenuOpen, searchOpen, setSearchOpen, className } = props
	
	const [query, setQuery] = useState('');
	const [response, setResponse] = useState({
		result: [],
		more: false
	});	

	const navigate = useNavigate();

	const inputField = useRef();
	useEffect(() => {
		setQuery('');
		setResponse({response: [], more:false});
		if(!searchOpen) return;
		inputField.current.focus();
	}, [searchOpen])

	const performSearch = (value) => {
		setQuery(value);
		if(!value) return;
		if(value.length < 3) {
			setResponse({response: [], more:false});
			return;
		}
		fetch(getUrl('', {
			controller: 'search',
			method: 'fast',
			q: value,
			lang: state.lang
		})).then((res) => res.json()).then((data) => {
			setResponse(data)
		}).catch((error) => {
			console.log(error)
		})
	}

	const openSearchPage = (event) => {
		if(event.key !== "Enter" || event.target.nodeName === 'I') return;
		gotoSearchPage()
	}

	const gotoSearchPage = () => {
		setQuery('')
		setSearchOpen(false);
		navigate('/search/'+query)
	}

	const openSearchResult = (link) => {
		setQuery('')
		navigate(link)
	}

	const backdropClicked = (event) => {
		console.log(event)
		if(event.target.nodeName === 'DIV') {
			setSearchOpen(false);
		}
	}


  return (<>
	<div className={"top-bar__search " + className}  >
		<div className="search__input">
		<input 
			type="search" 
			ref={inputField}
			placeholder="Suche"
			value={query} 
			onClick={(event) => {console.log(event)}}
			onChange={(event) => {performSearch(event.target.value)} }
			onKeyDown={(event) => {openSearchPage(event)}}
		/>
		<span className="search__send" onClick={(event) => {gotoSearchPage()}}><i className='material-icons'>search</i></span>
		</div>
		<div className={'search__results ' + (searchOpen ? 'search__results--visible' : '')} onClick={(event) => {backdropClicked(event)}}>
			<ul className='list list--padded'>
			{  response.result?.map((item) => {
			return <li className='list__item '>
				<a onClick={() => {openSearchResult(item.id)}}>{item.title}<br /><sub className='list__subtitle'>{item.abstract}</sub></a>
				</li>
		}) }
		</ul>
		</div>
	</div>
	
	</>
  )
}

export default MenuSearch;