import React, { useContext } from 'react';
import { getUrl } from '../services/api';
import { formatDate } from '../services/formatDate';
import { store } from '../services/store';
import __ from '../services/translate';
import FileIcon from './FileIcon';


const FileList = (props) => {

	const { files } = props;
	const { state } = useContext(store)
	const fileSize = (bytes) => {
		const thresh = 1024;

		if (Math.abs(bytes) < thresh) {
		  return bytes + ' B';
		}
	  
		const units = ['kB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
		let u = -1;
		const r = 10**1;
	  
		do {
		  bytes /= thresh;
		  ++u;
		} while (Math.round(Math.abs(bytes) * r) / r >= thresh && u < units.length - 1);
	  
	  
		return bytes.toFixed(1) + ' ' + units[u];
	}

	return (
		<ul className='list'>
			{ files?.map((file, key) => { 
				return <a key={key} className='list__item' href={getUrl(file.src, {lang: state.lang})}>
					<FileIcon size={16} className="list__image list__image--edgy list__image--small" extension={file.extension}/>
					<div className='list__content'>
						<span className='list__title text--black'>{file.file}</span>
						<span className='list__subtitle'>{fileSize(file.size)}, {__('changed')}: {formatDate(file.modified)}</span>
					</div>
					</a>
			}) }
		</ul>
	)
}

export default FileList