import React, { useContext } from 'react';
import { useParams } from "react-router-dom";
import FileList from '../partials/FileList';
import Navigation from '../partials/Navigation';
import { getUrl } from '../services/api';
import { formatDate } from '../services/formatDate';
import Parser from '../services/parser';
import __ from '../services/translate';
import useFetch from '../services/useFetch';

import { store } from './../services/store';

const Article = () => {

	const { state } = useContext(store);
	
	const params = useParams();
	let id = Object.values(params).join('/').replace(/\/+$/, '')
	if(id == '') id = 'start'
	
	const args = {
		controller: 'page',
		id,
		lang: state.lang
	}

	const url = getUrl(id, args);
	const { data, status } = useFetch(url)
	const page = data;

	 
	
	
	return (
		<>
		<Navigation />
		<header className="header" style={{paddingTop: '30vh'}}>
			
			{ page?.image && <img src={getUrl(page?.image?.src, {lang: state.lang})} /> }
			
			<div className="content">
				<div className="header__title">
					<h1 className="font-script">{page?.title}</h1>
					<h4 className="ml-4 xl:mr-0">{page?.subtitle}</h4>
				</div>
			</div>
		</header>
		<section className="bg-gray-100 py-12">
			{ status == 'success' &&
			<div className="content">
				<div className={page.files?.length > 0 ? 'grid grid--columns-1 md:grid--columns-2 xl:grid--columns-3 grid--gap-12' : ''}>
					<div className={page.files?.length > 0 ? 'grid__column grid__column--span-0 md:grid__column--span-0 xl:grid__column--span-2' : ''}><Parser content={page?.content} /></div>
					{page.files?.length > 0 && <div>
						<div className='card card--first card--last card--image-top card--shadow card--white'>
							<div className='card__content'>
								<div className='card__header'><h5 className='card__title'>{__("Downloads")}</h5></div>
								<div className='card__text'><FileList files={page.files}/></div>
							</div>
						</div>
					</div> }
				</div>
			</div>
			}
		</section>
		<section className='bg-gray-300 py-8'>
			{ page &&
			<div className='content'>
				<div className='pills'>
					<span>Schlagworte: </span>
					{ page?.tags?.map((tag, index) => {return <span className='bg-gray-800 pills__item pills__item--dark' key={index}>{tag}</span>}) }
				</div>
			{__("Zuletzt aktualisiert")}: {formatDate(page?.date)}
			
			</div>
}
		</section>
		</>
	)
}

export default Article